<template>
    <div>
        <el-form ref="form" :model="form" label-width="140px" :rules="rules">
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　基本信息" name="1">

                    <el-form-item label="vin号" prop="vin_number">
                        <el-input v-model="form.vin_number" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                
                    <el-form-item label="选择车型" prop="car_brand">
                        <el-input v-model="form.car_brand" v-show="false" :disabled="isDisabled()"></el-input>
                        <el-button @click="handleSelectCarType" :disabled="isDisabled()">车型选择</el-button>
                    </el-form-item>
                    <el-form-item label="车辆品牌">
                        {{form.car_brand}}
                    </el-form-item>
                    <el-form-item label="车辆系列">
                        {{form.car_series}}
                    </el-form-item>
                    <el-form-item label="车辆类型">
                        {{form.car_class}}
                    </el-form-item>
                    <el-form-item label="公告型号" prop="foreign_model">
                        <el-input v-model="form.foreign_model" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="燃油类型" prop="fuel_type">
                        <el-select v-model="form.fuel_type" placeholder="请选择燃油类型" class="mr10" :disabled="isDisabled()">
                            <el-option
                                v-for="item in optionsFuel"
                                :key="item.id"
                                :label="item.type_item_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="马力">
                        <el-input v-model="form.horse_power" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="驱动形式" prop="drive_form">
                        <el-select v-model="form.drive_form" placeholder="请选择驱动形式" class="mr10" :disabled="isDisabled()">
                            <el-option
                                v-for="item in optionsDrive"
                                :key="item.id"
                                :label="item.type_item_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="颜色" prop="color">
                        <el-input v-model="form.color" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="绑定车主">
                        {{form.band_name}}
                        <el-button @click="handleSelectClient('default')" :disabled="isDisabled()">绑定车主</el-button>
                    </el-form-item>
                    <el-form-item label="销售金额" prop="sale_amount">
                        <el-input v-model="form.sale_amount" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="选择挂靠公司">
                        <!-- {{form.bus_mech_id}} -->
                        <!-- <el-button @click="handleSelectClient('company')">选择挂靠公司</el-button> -->
                        <!-- <el-tree
                            ref="domTree"
                            :data="dataTree"
                            show-checkbox
                            node-key="id"
                            @check="handleClick"
                            default-expand-all
                            :expand-on-click-node="false">
                            <span class="custom-tree-node" slot-scope="{ node, data }">
                                <span>{{ data.mc_name }}</span>
                            </span>
                        </el-tree> -->
                        <el-select v-model="form.bus_mech_id" placeholder="请选择挂靠公司" class="mr10" :disabled="isDisabled()">
                            <el-option
                                v-for="item in optionsCompany"
                                :key="item.id"
                                :label="item.mc_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="车牌号">
                        <el-input v-model="form.plate_number" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="事故理赔" prop="accident_claim">
                        <el-input v-model="form.accident_claim" :disabled="isDisabled()">
                            <i slot="suffix" class="mr5">次</i>
                        </el-input>
                    </el-form-item>
                    <el-form-item label="重大事故" prop="is_major">
                        <el-radio-group v-model="form.is_major" :disabled="isDisabled()">
                            <el-radio label="1">没有</el-radio>
                            <el-radio label="2">有</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="历史累计理赔金额" prop="history_claim_amont">
                        <el-input v-model="form.history_claim_amont" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="协助处理违章" prop="assist_deal">
                        <el-input v-model="form.assist_deal" :disabled="isDisabled()">
                            <i slot="suffix" class="mr5">次</i>
                        </el-input>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　认证信息" name="2">
                    
                    <el-form-item label="合格证照片">
                        <el-upload
                            :disabled="isDisabled()"
                            class="avatar-uploader"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="handleUploadSuccess_cert_img"
                            :before-upload="handleBeforeUpload">
                            <img v-if="form.cert_img" :src="form.cert_img" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="合格证号">
                                <el-input v-model="form.cert_number" :disabled="isDisabled()"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-form-item label="购车发票照片">
                        <el-upload
                            :disabled="isDisabled()"
                            class="avatar-uploader"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="handleUploadSuccess_buy_invoice"
                            :before-upload="handleBeforeUpload">
                            <img v-if="form.buy_invoice" :src="form.buy_invoice" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>

                    <el-form-item label="销售发票照片">
                        <el-upload
                            :disabled="isDisabled()"
                            class="avatar-uploader"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="handleUploadSuccess_sale_invoice"
                            :before-upload="handleBeforeUpload">
                            <img v-if="form.sale_invoice" :src="form.sale_invoice" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="购车发票号">
                                <el-input v-model="form.purchase_invoice_number" :disabled="isDisabled()"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="销售发票号">
                                <el-input v-model="form.sale_invoice_number" :disabled="isDisabled()"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-form-item label="挂靠协议">
                        <el-upload
                            :disabled="isDisabled()"
                            :action="IMG_UPLOAD_URL"
                            :on-success="handleUploadSuccess_call_pro"
                            :file-list="form.call_pro"
                            list-type="picture-card">
                            <i class="el-icon-plus"></i>
                        </el-upload>
                    </el-form-item>

                    <el-form-item label="安全协议">
                        <el-upload
                            :disabled="isDisabled()"
                            :action="IMG_UPLOAD_URL"
                            :on-success="handleUploadSuccess_safe_pro"
                            :file-list="form.safe_pro"
                            list-type="picture-card">
                            <i class="el-icon-plus"></i>
                        </el-upload>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　资格信息" name="3">
                    
                    <el-form-item label="驾驶证照片">
                        <el-upload
                            :disabled="isDisabled()"
                            class="avatar-uploader"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="handleUploadSuccess_drive_img"
                            :before-upload="handleBeforeUpload">
                            <img v-if="form.drive_img" :src="form.drive_img" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>

                    <el-form-item label="行驶证号">
                        <el-input v-model="form.drive_license_number" :disabled="isDisabled()"></el-input>
                    </el-form-item>

                    <el-form-item label="行驶证注册日期">
                        <el-date-picker
                            :disabled="isDisabled()"
                            v-model="form.drive_license_register"
                            type="date"
                            placeholder="选择日期">
                        </el-date-picker>
                    </el-form-item>

                    <el-form-item label="行驶证到期日期">
                        <el-date-picker
                            :disabled="isDisabled()"
                            v-model="form.drive_license_expire"
                            type="date"
                            placeholder="选择日期">
                        </el-date-picker>
                    </el-form-item>

                    <el-form-item label="营运证照片">
                        <el-upload
                            :disabled="isDisabled()"
                            class="avatar-uploader"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="handleUploadSuccess_op_img"
                            :before-upload="handleBeforeUpload">
                            <img v-if="form.op_img" :src="form.op_img" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>

                    <el-form-item label="营运证号">
                        <el-input v-model="form.op_license_number" :disabled="isDisabled()"></el-input>
                    </el-form-item>

                    <el-form-item label="营运证注册日期">
                        <el-date-picker
                            :disabled="isDisabled()"
                            v-model="form.op_license_register"
                            type="date"
                            placeholder="选择日期">
                        </el-date-picker>
                    </el-form-item>

                    <el-form-item label="营运证到期日期">
                        <el-date-picker
                            :disabled="isDisabled()"
                            v-model="form.op_license_expire"
                            type="date"
                            placeholder="选择日期">
                        </el-date-picker>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　保险信息" name="4">
                    

                    <el-card class="box-card mb10" v-for="(item, index) in form.insr_info">
                        <el-row>
                            <el-col :span="12">
                                <el-form-item :label="index==0?'交强险保单照片':'保单照'">
                                    <el-upload
                                        :disabled="isDisabled()"
                                        class="avatar-uploader"
                                        :action="IMG_UPLOAD_URL"
                                        :show-file-list="false"
                                        :on-success="(res,file)=>{return handleUploadSuccess_insurance_img(res,file,index)}"
                                        :before-upload="handleBeforeUpload">
                                        <img v-if="item.insurance_img" :src="item.insurance_img" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                    </el-upload>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item :label="index==0?'交强险发票照片':'发票照片'">
                                    <el-upload
                                        :disabled="isDisabled()"
                                        class="avatar-uploader"
                                        :action="IMG_UPLOAD_URL"
                                        :show-file-list="false"
                                        :on-success="(res,file)=>{return handleUploadSuccess_insurance_invoice(res,file,index)}"
                                        :before-upload="handleBeforeUpload">
                                        <img v-if="item.insurance_invoice" :src="item.insurance_invoice" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                    </el-upload>
                                </el-form-item>
                            </el-col>
                        </el-row>

                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="保险单号">
                                    <el-input v-model="item.insurance_number" :disabled="isDisabled()"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="保险公司">
                                    <el-select v-model="item.insurance_company" placeholder="请选择保险公司" class="mr10" :disabled="isDisabled()">
                                        <el-option
                                            v-for="item in optionsInsuranceCompany"
                                            :key="item.id"
                                            :label="item.insurance_name"
                                            :value="item.insurance_name">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                        </el-row>

                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="保险生效日期">
                                    <el-date-picker
                                        :disabled="isDisabled()"
                                        v-model="item.insurance_effect"
                                        type="date"
                                        placeholder="选择日期">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="保险到期日期">
                                    <el-date-picker
                                        :disabled="isDisabled()"
                                        v-model="item.insurance_expire"
                                        type="date"
                                        placeholder="选择日期">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        
                    </el-card>

                    <div class="toolbar mb10">
                        <el-button type="primary" size="mini" plain @click="handleAddInsurance" :disabled="isDisabled()">新增保险</el-button>
                    </div>
                    

                </el-collapse-item>

            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" @click="handleSubmit" v-if="pageType != 'view'">保存</el-button>
                <el-button @click="$router.back()">返回</el-button>
            </el-form-item>
        </el-form>

        <!-- 选择车型 -->
        <el-dialog title="选择车型" :visible.sync="dialogSelectCarType" width="50%">
            <selectCar v-if="hackReset" :selectCarOptions="selectCarOptions" @emitCarSubmit="emitCarSubmit" @emitCarClose="emitCarClose"></selectCar>
        </el-dialog>

        <!-- 选择客户 -->
        <el-dialog title="选择客户" :visible.sync="dialogSelectClient" width="50%">
            <selectClient v-if="hackResetClient" :selectClientOptions="selectClientOptions" @emitClientSubmit="emitClientSubmit" @emitClientClose="emitClientClose"></selectClient>
        </el-dialog>

    </div>
</template>

<script>
    import * as API from '@/api/assetsFile'
    import { organizationList, organizationListUl } from '@/api/enterpriseManagement'
    import {apiGetFuelTypes,apiGetDriveTypes} from '@/api/resourceManagement'
    import { requestUrl } from '@/api/config'
    import selectCar from '@/components/selectCar'
    import selectClient from '@/components/selectClient'
    import * as VALIDATE from '@/assets/utils/validate'
    export default {
        data: function(){
            const data = [];
            return {
                IMG_UPLOAD_URL: `${requestUrl}/home/UpLoadFile/uploadImg`,
                collapseActive: ['1'],
                dataTree: JSON.parse(JSON.stringify(data)),
                optionsCompany: [],
                optionsInsuranceCompany: [],
                optionsFuel:[],
                optionsDrive:[],
                hackReset: false,
                dialogSelectCarType: false,
                pageType: '',
                selectCarOptions:{
                    tableConfig:{
                        bus_brand: true,
                        bus_type: true,
                        bus_series: true
                    },
                    formConfig: {
                        val: false
                    },
                    pageType: 'carTypeSelect'
                    
                },
                hackResetClient: false,
                dialogSelectClient: false,
                selectClientOptions:{
                    formConfig: {
                        val: false
                    },
                    pageType: 'default'
                    
                },
                form: {
                    accident_claim: "",
                    assist_deal: "",
                    band_name: "",
                    bus_mech_id: "",
                    buy_invoice: "",
                    call_pro: [],
                    car_brand: "",
                    cert_img: "",
                    cert_number: "",
                    color: "",
                    create_time: "",
                    create_user: "",
                    drive_form: "",
                    drive_img: "",
                    drive_license_expire: "",
                    drive_license_number: "",
                    drive_license_register: "",
                    foreign_model: "",
                    fuel_type: "",
                    history_claim_amont: "",
                    horse_power: "",
                    is_major: "1",
                    op_img: "",
                    op_license_expire: "",
                    op_license_number: "",
                    op_license_register: "",
                    plate_number: "",
                    purchase_invoice_number: "",
                    safe_pro: [],
                    sale_amount: "",
                    sale_invoice: "",
                    sale_invoice_number: "",
                    vin_number: "",
                    insr_info:[
                        {
                            insurance_img: "",
                            insurance_invoice: "",
                            insurance_expire: "",
                            insurance_number: "",
                            insurance_company: "",
                        }
                    ]
                },
                rules: {
                    vin_number: [
                        { required: true, message: '请输入vin号', trigger: 'blur' }
                    ],
                    car_brand: [
                        { required: true, message: '请选择车型', trigger: 'change' }
                    ],
                    foreign_model: [
                        { required: true, message: '请输入公告型号', trigger: 'blur' }
                    ],
                    color: [
                        { required: true, message: '请输入颜色', trigger: 'blur' }
                    ],
                    sale_amount: [
                        { required: true, message: '请输入销售金额', trigger: 'blur' }
                    ],
                    accident_claim: [
                        { required: true, message: '请输入事故理赔', trigger: 'blur' },
                        { validator:VALIDATE.number, trigger: 'blur' }
                    ],
                    is_major: [
                        { required: true, message: '请选择重大事故', trigger: 'change' }
                    ],
                    history_claim_amont: [
                        { required: true, message: '请输入历史累计理赔金额', trigger: 'blur' }
                    ],
                    assist_deal: [
                        { required: true, message: '请输入协助处理违章', trigger: 'blur' }
                    ]
                }
            }
        },
        components:{
            selectCar,
            selectClient
        },
        mounted(){
            this.checkPath()
            this.getCompany()
            this.getInsuranceCompany()
            this.getFuel()
            this.getDrive()
        },
        methods: {
            // 是否禁止输入
            isDisabled () {
                let d = this.pageType == 'view' ? true : false;
                return d;
            },
            checkPath() {
                let _path = this.$route.path,
                    _query = this.$route.query;
                if(_path.indexOf('add') > -1) {
                    this.pageType = 'add';
                }else if(_path.indexOf('edit') > -1) {
                    this.pageType = 'edit';
                    this.getFormData();
                }else if(_path.indexOf('view') > -1) {
                    this.pageType = 'view';
                    this.getFormData();
                }
            },
            getFormData(){
                API.apiCarFileView({id: this.$route.query.id}).then(res => {
                    Object.assign(this.form,res.data)
                })
            },
            // 公司列表
            getCompany(){
                organizationListUl({}).then(res => {
                    this.optionsCompany = res.data
                })
            },
            // 获取保险公司
            getInsuranceCompany(){
                API.apiGetInsuranceCompany({}).then(res => {
                    this.optionsInsuranceCompany = res.data
                })
            },
            getFuel(){
                apiGetFuelTypes({}).then(res => {
                    this.optionsFuel = res.data
                })
            },
            getDrive(){
                apiGetDriveTypes({}).then(res => {
                    this.optionsDrive = res.data
                })
            },
            getTreeList(){
                organizationList({}).then(res => {
                    this.dataTree = JSON.parse(JSON.stringify(res.data))
                })
            },
            handleUploadSuccess_cert_img(res, file, fileList){
                this.form.cert_img = res.data.url
            },
            handleUploadSuccess_buy_invoice(res, file, fileList){
                this.form.buy_invoice = res.data.url
            },
            handleUploadSuccess_sale_invoice(res, file, fileList){
                this.form.sale_invoice = res.data.url
            },
            handleUploadSuccess_call_pro(res, file, fileList){
                this.form.call_pro.push({
                    uid: file.uid,
                    url: res.data.url
                })
            },
            handleUploadSuccess_safe_pro(res, file, fileList){
                this.form.safe_pro.push({
                    uid: file.uid,
                    url: res.data.url
                })
            },
            handleUploadSuccess_drive_img(res, file, fileList){
                this.form.drive_img = res.data.url
            },
            handleUploadSuccess_op_img(res, file, fileList){
                this.form.op_img = res.data.url
            },
            handleUploadSuccess_insurance_img(res,file,index){
                this.form.insr_info[index].insurance_img = res.data.url
            },
            handleUploadSuccess_insurance_invoice(res, file, index){
                this.form.insr_info[index].insurance_invoice = res.data.url
            },
            handleBeforeUpload(){

            },
            handleSubmit() {
                this.$refs.form.validate((valid)=> {
                    if(valid){
                        if(this.pageType == 'add'){
                            API.apiCarFileAdd(this.form).then(res => {
                                this.$message.success('提交成功！');
                                this.$router.back()
                            })
                        }else{
                            API.apiCarFileEdit(this.form).then(res => {
                                this.$message.success('提交成功！');
                                this.$router.back()
                            })
                        }
                    }else{
                        return false
                    }
                })
            },
            handleClick(item, items){
                this.form.bus_mech_id = items.checkedKeys
            },
            handleAddInsurance(){
                this.form.insr_info.push({
                    insurance_img: "",
                    insurance_invoice: "",
                    insurance_expire: "",
                    insurance_number: "",
                    insurance_company: "",
                })
            },
            handleSelectCarType(){
                this.dialogSelectCarType = true
                this.hackReset = false
                this.$nextTick(() => {
                    this.hackReset = true
                })
            },
            emitCarClose(){
                this.dialogSelectCarType = false;
            },
            emitCarSubmit(row){
                this.dialogSelectCarType = false
                this.form.car_brand = row.bus_brand
                this.form.car_series = row.bus_series
                this.form.car_class = row.bus_type
                this.form.mcar_id = row.id
            },
            handleSelectClient(pageType){
                this.dialogSelectClient = true
                this.selectClientOptions.pageType = pageType
                this.hackResetClient = false
                this.$nextTick(() => {
                    this.hackResetClient = true
                })

            },
            emitClientClose(){
                this.dialogSelectClient = false;
            },
            emitClientSubmit(row){
                this.dialogSelectClient = false
                if(row.name){
                    this.form.band_name = row.name
                    this.form.band_id = row.id
                }else{
                    this.form.band_name = row.company_name
                    this.form.band_com_id = row.id
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    
</style>